.App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .React-showcase{
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: calc(8px + 2vmin);
    color: white;
    flex: 2;
    flex-direction: row;
    flex-wrap: wrap;
    width: 25%;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }